import { Button, ButtonEnums } from '@ohif/ui';
import React from 'react';
import { saveAs } from 'file-saver';
import JSZip from 'jszip';
import {
  segmentation as cstSegmentation,
  Enums as cstEnums,
  Types as cstTypes,
} from '@cornerstonejs/tools';
import { DicomMetadataStore, utils } from '@ohif/core';
import axios, { AxiosRequestConfig } from 'axios';
import pako from 'pako';

function SegmentationImportExport({
  segmentationService,
  uiNotificationService,
  hangingProtocolService,
  viewportGridService,
  cornerstoneCacheService,
  displaySetService,
}: withAppTypes): React.ReactNode {
  const exportActiveSegmentation = async () => {
    let segmentation = segmentationService.getActiveSegmentation('default');
    if (!segmentation) {
      uiNotificationService.show({
        title: `Export Active Segmentation`,
        message: `No active segmentation detected`,
        type: 'info',
        duration: 3000,
      });
      return;
    }

    const { segments: segmentsInfo, label: segmentationLabel, segmentationId } = segmentation;
    const { dimensions, voxelManager } = segmentationService.getLabelmapVolume(
      segmentation.segmentationId
    );
    const scalarData = voxelManager.getCompleteScalarDataArray();
    const layerSize = dimensions[0] * dimensions[1];
    const nbLayers = dimensions[2];

    // One zip file per segmentation
    const zip = new JSZip();

    // One PNG image per layer
    for (let layerI = 0; layerI < nbLayers; layerI++) {
      const pixelData = new Uint8ClampedArray(layerSize * 4).fill(0);
      for (let i = 0; i < layerSize; i++) {
        const segmentIndex = scalarData[layerI * layerSize + i];
        if (segmentIndex !== 0) {
          // Not background
          const color = segmentationService.getSegmentColor(
            'default',
            segmentationId,
            segmentIndex
          );
          pixelData[i * 4] = color[0];
          pixelData[i * 4 + 1] = color[1];
          pixelData[i * 4 + 2] = color[2];
        }
        pixelData[i * 4 + 3] = 255;
      }

      // Store PNG image in zip
      const canvas = document.createElement('canvas');
      const ctx = canvas.getContext('2d');
      canvas.width = dimensions[0];
      canvas.height = dimensions[1];
      const imgData = new ImageData(pixelData, dimensions[0], dimensions[1], {
        colorSpace: 'srgb',
      });
      ctx.putImageData(imgData, 0, 0);
      ctx.putImageData(imgData, 0, 0);

      const pngBlob = await new Promise<Blob>((resolve, reject) => {
        canvas.toBlob(
          blob => (blob ? resolve(blob) : reject(new Error('Conversion to blob failed'))),
          'image/png'
        );
      });
      zip.file(`layer_${layerI}.png`, pngBlob);
    }

    // Add scalar data as binary file
    const binaryBlob = new Blob(
      [new Uint8ClampedArray(voxelManager.getCompleteScalarDataArray())],
      {
        type: 'application/octet-stream',
      }
    );
    zip.file(`labelmapVolume_${segmentationLabel}.bin`, binaryBlob);

    // Add segments information as JSON File
    const jsonBlob = new Blob([JSON.stringify(segmentsInfo, null, '\t')], {
      type: 'text/plain;charset=utf-8',
    });
    zip.file(`segmentsInfo_${segmentationLabel}.json`, jsonBlob);

    // Get study metadata
    const { activeStudyUID } = hangingProtocolService.getState();
    const study = DicomMetadataStore.getStudy(activeStudyUID);
    const { StudyDate, PatientName } = study?.series[0]?.instances[0];
    const patientNameFormatted = utils.formatPN(PatientName[0]);

    // Save zip file
    const content = await zip.generateAsync({ type: 'blob' });
    saveAs(content, `${StudyDate ?? ''}_${patientNameFormatted ?? ''}_${segmentationLabel}.zip`);
  };

  const importConfiguration = configurationFile => {
    // Default viewport needs to be accessible
    const displaySetUID = viewportGridService.getDisplaySetsUIDsForViewport('default')[0];
    if (!displaySetUID) {
      uiNotificationService.show({
        title: `Import Segmentation`,
        message: `Default viewport not found`,
        type: 'info',
        duration: 3000,
      });
      return;
    }

    const reader = new FileReader();
    // Read configuration file
    reader.onload = async function (event) {
      try {
        if (typeof event.target.result !== 'string') {
          // TODO error message
          console.error('ERROR::JSON_READING::CORRUPTED_CONTENT');
          return;
        }
        // Copy segmentation
        // TODO does not handle _ in segmentation label
        const segmentationLabel = configurationFile.name
          .split('_')
          .pop()
          .replace(/\.[^/.]+$/, '');
        const displaySet = displaySetService.getDisplaySetByUID(displaySetUID);
        const segments = JSON.parse(event.target.result);
        const options = { segments, label: segmentationLabel };
        const segId = await segmentationService.createLabelmapForDisplaySet(displaySet, options);
        await segmentationService.addSegmentationRepresentation('default', {
          segmentationId: segId,
          type: cstEnums.SegmentationRepresentations.Labelmap,
        });
        segmentationService.setActiveSegmentation('default', segId);
      } catch (error) {
        // TODO error message
        console.error('ERROR::JSON_READING', error);
      }
    };
    reader.readAsText(configurationFile);
  };

  const importBinary = async binaryFile => {
    // TODO promise?
    const reader = new FileReader();
    // Read binary file
    reader.onload = async function (event) {
      const newScalarData = new Uint8Array(event.target.result as ArrayBuffer);
      const segmentationLabel = binaryFile.name
        .split('_')
        .pop()
        .replace(/\.[^/.]+$/, '');
      const segmentations = segmentationService.getSegmentations() as cstTypes.Segmentation[];
      const segmentation = segmentations.filter(s => s.label === segmentationLabel)[0];
      if (!segmentation) {
        uiNotificationService.show({
          title: `Import Segmentation`,
          message: `Segmentation with name ${segmentationLabel} does not match segmentation of configuration file`,
          type: 'info',
          duration: 3000,
        });
        return;
      }

      const { voxelManager } = segmentationService.getLabelmapVolume(segmentation.segmentationId);
      voxelManager.setCompleteScalarDataArray(newScalarData);
      cstSegmentation.triggerSegmentationEvents.triggerSegmentationDataModified(
        segmentation.segmentationId
      );
    };
    reader.readAsArrayBuffer(binaryFile);
  };

  const importSegmentationFromFolder = () => {
    // Default viewport needs to be accessible
    const displaySetUID = viewportGridService.getDisplaySetsUIDsForViewport('default')[0];
    if (!displaySetUID) {
      uiNotificationService.show({
        title: `Import Segmentation`,
        message: `Default viewport not found`,
        type: 'info',
        duration: 3000,
      });
      return;
    }

    // Load binary file
    const input = document.createElement('input');
    input.type = 'file';
    input.webkitdirectory = true;
    input.onchange = async _ => {
      const files = Array.from(input.files);
      const configurationFile = files.filter(filter => filter.type === 'application/json')[0];
      const binaryFile = files.filter(file => file.type === 'application/octet-stream')[0];
      if (!configurationFile || !binaryFile) {
        uiNotificationService.show({
          title: `Import Segmentation`,
          message: `Missing configuration or binary file`,
          type: 'info',
          duration: 3000,
        });
        return;
      }
      importConfiguration(configurationFile);
      importBinary(binaryFile);
    };
    input.click();
  };

  const exportGeneratedSegmentation = async () => {
    const { cbctSegmentationUUID, accessKey } =
      cornerstoneCacheService.getCbctSegmentationMetadata();
    if (!cbctSegmentationUUID || !accessKey) {
      uiNotificationService.show({
        title: `Export Generated Segmentation`,
        message: `No credentials found`,
        type: 'info',
        duration: 3000,
      });
      return;
    }

    const generatedSegmentation = segmentationService.getGeneratedSegmentation();
    if (!generatedSegmentation) {
      uiNotificationService.show({
        title: `Export Generated Segmentation`,
        message: `No generated segmentation detected`,
        type: 'info',
        duration: 3000,
      });
      return;
    }
    const { voxelManager } = segmentationService.getLabelmapVolume(
      generatedSegmentation.segmentationId
    );
    const segmentationConfig = JSON.stringify(generatedSegmentation.segments);

    try {
      const formData: FormData = new FormData();
      const volumeArray = pako.deflate(voxelManager.getCompleteScalarDataArray());
      formData.append('modified_volume', new Blob([volumeArray]), `${cbctSegmentationUUID}.zlib`);
      formData.append('modified_config', segmentationConfig);
      const url = `https://localhost/api/v1/cbct/${cbctSegmentationUUID}/`;
      const headers = accessKey ? { Authorization: 'Bearer ' + accessKey } : undefined;
      const config: AxiosRequestConfig = { method: 'put', url, headers, data: formData };
      const response = await axios(config);
      console.log('Server response for segmented volume export', response.data);

      return response;
    } catch (error) {
      console.error('Server error for segmented volume export', error);
    }
  };

  return (
    <div className="font-inter flex min-h-0 flex-col font-[300]">
      <div className="bg-primary-dark pb-[6px] pt-[6px]">
        <p className="flex pb-[6px] pl-[9px] pr-[9px] text-[12px] font-[300] text-[#d8d8d8]">
          {'Segmentation'}
        </p>
        <div className="flex justify-evenly text-[12px] font-[300] text-[#d8d8d8]">
          <Button
            size={ButtonEnums.size.small}
            onClick={importSegmentationFromFolder}
          >
            {'Import'}
          </Button>
          <Button
            size={ButtonEnums.size.small}
            onClick={exportActiveSegmentation}
          >
            {'Export'}
          </Button>
        </div>
        <p className="flex pb-[6px] pl-[9px] pr-[9px] text-[12px] font-[300] text-[#d8d8d8]">
          {'Generated segmentation'}
        </p>
        <div className="flex justify-evenly text-[12px] font-[300] text-[#d8d8d8]">
          <Button
            size={ButtonEnums.size.small}
            onClick={exportGeneratedSegmentation}
          >
            {'Export generated segmentation'}
          </Button>
        </div>
      </div>
    </div>
  );
}

export default SegmentationImportExport;
