import { Button, ButtonEnums } from '@ohif/ui';
import React from 'react';
import { Enums as csEnums, VolumeViewport3D } from '@cornerstonejs/core';
import { VolumeData } from 'extensions/cornerstone/src/types/CornerstoneCacheService';

function SegmentationConversion({
  segmentationService,
  cornerstoneCacheService,
  cornerstoneViewportService,
}: withAppTypes): React.ReactNode {
  const addOrUpdateSurface = async () => {
    const { volume } = cornerstoneViewportService.getViewportInfo('default').getViewportData()
      .data[0] as VolumeData;
    const origin = volume.imageData.getOrigin();

    const surfaceActorEntries = [];
    for (const segmentation of segmentationService.getAxialSegmentations()) {
      const segmentationSurfaceActorEntries = await segmentationService.createSurfacesData(
        segmentation,
        origin
      );
      surfaceActorEntries.push(...segmentationSurfaceActorEntries);
    }
    // TODO stop storing surfaceActorEntries in cornerstoneCacheService
    cornerstoneCacheService.setSurfaceActorEntries(surfaceActorEntries);

    // Display surfaces in 3D viewport
    const viewportIds = cornerstoneViewportService.getViewportIds();
    const volumeViewportId = viewportIds.find(
      vpId =>
        cornerstoneViewportService.getViewportInfo(vpId).getViewportType() ===
        csEnums.ViewportType.VOLUME_3D
    );
    if (volumeViewportId) {
      const volumeViewport = cornerstoneViewportService.getCornerstoneViewport(
        volumeViewportId
      ) as VolumeViewport3D;
      cornerstoneViewportService.displaySurfaceActors(volumeViewport);
      volumeViewport.render();
    }
  };

  return (
    <div className="font-inter flex min-h-0 flex-col font-[300]">
      <div className="bg-primary-dark pb-[6px] pt-[6px]">
        <p className="flex pb-[6px] pl-[9px] pr-[9px] text-[12px] font-[300] text-[#d8d8d8]">
          {'Segmentation 3D'}
        </p>
        <div className="flex justify-evenly text-[12px] font-[300] text-[#d8d8d8]">
          <Button
            size={ButtonEnums.size.small}
            onClick={addOrUpdateSurface}
          >
            {'Convert segmentation to surface'}
          </Button>
        </div>
      </div>
    </div>
  );
}

export default SegmentationConversion;
